@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

/* width */
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #ddd;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #111D5E60;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #111D5E;
}
.header{
    background-color: #111D5E;
    height: 70px;
    position: fixed;
    width: 100%;
    z-index: 23;
}
.navbar{
    display: flex;
    justify-content: space-between;
    max-width: 1088px;
    margin: auto;
    align-items: center;
}

.header .navbar .navLinks{
    display: flex;
    margin-top: -10px;
    padding: 0px;
}
.header .navbar .navLinks ul{
    list-style: none;
    display: flex;
    margin: 0px;
    margin: auto;
    padding: 0px;
}
.header .navbar .navLinks ul li{
    color: #fff;
    margin: 25px;
    font-size: 14px;
    cursor: pointer;
}
.header .navbar .navLinks ul li:hover{
    color: #8396ffb2;
}
.header .mobileNav{
    display: none;
}
@media only screen and (max-width: 768px) {
    .header .navbar .navLinks{
        display: none;
    }
    .header .navbar .searchComponent{
        display: none;
    }
    .header .navbar img{
        margin: 18px 10px;
    }
    .mobileSearchBar{
        color: #fff;
    }
    .header .mobileNav{
        display: flex;
        align-items: center;
    }
}


/* footer section */

.footer{
    margin: 0;
    padding: 0;
}
.footer .containerOne{
    margin: auto;
    background: #131f6408;
}
.footer .containerOne .footerRowOne{
    max-width: 1103px;
    padding: 15px;
    margin: auto;
    text-align: left;
}
.footer .containerOne .footerRowOne .footerColOne{
    margin: 52px 0;
    padding: 0;
}
.footer .containerOne .footerRowOne h5{
    font-size: 14px;
    font-weight: 700;
    color: rgba(0, 0, 0, 0.6);
    font-family: 'Montserrat', sans-serif;
}
.footer .containerOne .footerRowOne p{
    font-size: 14px;
    color: #26262640;
    max-width: 330px;
    margin: 20px 0;
}
.footer .containerOne .footerRowOne input{
    width: 210px;
    height: 40px;
    border: 1px solid rgba(119, 119, 119, 0.507);
    border-radius: 4px;
    background: none;
    margin-right: 15px;
    padding: 10px;
    outline: none;
}
.footer .containerOne .footerRowOne input::-webkit-input-placeholder{
    font-size: 14px;
    color: #A2A2A2;
}
.footer .containerOne .footerRowOne input:-ms-input-placeholder{
    font-size: 14px;
    color: #A2A2A2;
}
.footer .containerOne .footerRowOne input::placeholder{
    font-size: 14px;
    color: #A2A2A2;
}
.footer .containerOne .footerRowOne button{
    height: 40px;
    width: 120px;
    background: #F37121;
    color: white;
    font-size: 14px;
    font-weight: bold;
    border-radius: 4px;
    border-width: 0px;
}
.footer .containerOne .footerRowOne button:focus{
    outline: none;
}
.footer .containerOne .footerRowOne ul{
    list-style: none;
    margin: 20px 0;
    padding: 0;
}
.footer .containerOne .footerRowOne ul li {
    margin-bottom: 10px;
    font-size: 14px;
    color: #26262640;
    cursor: pointer;
}
.footer .containerOne .footerRowOne ul li:hover{
    color: #111D5E;
}
.footer .containerTwo{
    background: #111D5E;
    margin: 0px;
    padding: 0px;
}
.footer .containerTwo .subContainer{
    max-width: 1103px;
    display: flex;
    justify-content: space-between;
    margin: 15px;
    margin: auto;
    align-items: center;
}
.footer .containerTwo .subContainer h6{
    color: #fff;
    padding: 20px;
    margin: 0px;
}
.footer .containerTwo .subContainer .socialIcon{
    color: #fff;
    margin: 10px;
    font-size: 20px;
}
@media only screen and (max-width: 600px){
    .footer .containerOne .footerRowOne{
        text-align: center;
    }
}

/* post slider */
.postSlider{
    width: 100%;
    height: 100vh;
    align-items: center;
}
.postSlider img{
    height: 100%;
    width: auto;
}
@media only screen and (max-width: 768px){
    .postSlider img{
        height: 100%;
        width: auto;
        margin: auto;
    }
}
/* allpost section */
.allPost{
    margin: 15px;
    width: 100%;
    max-width: 320px;
    text-align: left;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}
.allPost img{
    width: 100%;
    height: auto;
}
.allPostListRow{
    display: flex;
    flex-wrap: wrap;
    max-width: 1088px;
    margin: auto;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}
.allPost .info{
    display: flex;
}
.allPost .info p{
    padding-right: 30px;
    font-size: 13px;
    color: #3D3D3D60;
    /* border-right: 1px solid rgba(20, 13, 13, 0.221); */
    margin-top: 16px;
}
.allPost h2{
    font-size: 24px;
    color: #3D3D3D;
}
.allPost h3{
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
    margin-top: 24px;
    margin-bottom: 24px;
    font-size: 16px;
    color: #737373;
}
.allPostListRow button{
    background: #F37121;
    color: white;
    font-weight: bold;
    border-width: 0px;
    width: 200px;
    height: 48px;
    margin: auto;
    margin-bottom: 40px;
    margin-top: 40px;
    outline: none;
}
.allPostListRow button:focus{
    outline: none;
}

/* subscribe section */
.subscribe{
    background-size: cover;
    background-position: center;
    background: url('https://res.cloudinary.com/dmixz7eur/image/upload/v1611229692/black%20lives/Background_STYLESTYLE3_d6c2vy.jpg');
    margin: 0px;
    width: 100%;
    padding: 0%;
}
.subscribe .container{
    background: linear-gradient(to right, #5E5E5E00, #53535312, #07070792, #000000);
    width: 100%;
    max-width: 2500px;
}
.subscribe .container .subscribeRow{
    max-width: 1100px;
    margin: auto;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
}
.subscribe .container .subscribeRow h1{
    font-size: 30px;
    color: #fff;
    margin-top: 80px;
    margin-bottom: 80px;
}
.subscribe .container .subscribeRow form{
    display: block;
}
.subscribe .container .subscribeRow form input{
    width: 260px;
    height: 48px;
    background: none;
    padding: 10px;
    font-size: 14px;
    color: #ffffff60;
    border: 1px solid #ffffff80;
    margin: 20px;
    outline: none;
}
.subscribe .container .subscribeRow form input::-webkit-input-placeholder{
    color: #ffffff80;
}
.subscribe .container .subscribeRow form input:-ms-input-placeholder{
    color: #ffffff80;
}
.subscribe .container .subscribeRow form input::placeholder{
    color: #ffffff80;
}
.subscribe .container .subscribeRow form button{
    background: #F37121;
    color: white;
    font-weight: bold;
    width: 260px;
    height: 48px;
    border-width: 0px;
    outline: none;
}
@media only screen and (max-width: 768px){
    .subscribe .container .subscribeRow h1{
        margin-top: 30px;
        margin-bottom: 30px;
    }
    .subscribe .container .subscribeRow form button{
        margin-bottom: 30px;
    }
}
/* main dashboard */
.mainDashboard{
    background: #F1F0FA;
    margin: 0px;
    padding: 0px;
    height: 100%;
    height: 100vh;
    overflow-y: hidden;
}
.mainDashboard .dashboardRow{
    max-width: 1920px;
    height: 100%;
    margin: 0px;
    margin: auto;
    background: #F1F0FA;
}
.mainDashboard .dashboardRow .dashboardCol{
    margin: 0px;
    background: #F1F0FA;
    padding: 0px;
    height: 100%;
    min-height: 100vh;
}
.mainDashboard .dashboardRow .containerOne{
    background: #fff;
    margin-left: 0px;
    height: 100%;
    min-height: 100vh;
}
.mainDashboard .dashboardRow .containerOne .profile{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.mainDashboard .dashboardRow .containerOne .profile .profileInfo{
    display: flex;
    align-items: center;
}
.mainDashboard .dashboardRow .containerOne .profile img{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin: 20px;
    margin-right: 10px;
}
.mainDashboard .dashboardRow .containerOne .profile .profileAbout{
    margin: 0px;
    align-items: center;
    padding: 0px;
}
.mainDashboard .dashboardRow .containerOne .profile h3{
    font-size: 18px;
    color: #111D5E;
    margin: 0px;
}
.mainDashboard .dashboardRow .containerOne .profile h6{
    color: #3A537860;
    font-size: 14px;
    margin-top: 2px;
}
.mainDashboard .dashboardRow .adminSections .tabs{
    display: flex;
    align-items: center;
    height: 51px;
    color: #00000060;
    justify-content: space-between;
    padding-left: 25px;
    padding-right: 30px;
    text-align: left;
    cursor: pointer;
}
.mainDashboard .dashboardRow .adminSections .tabs.active{
    color: #111D5E;
    border-left: 5px solid #111D5E;
}
.mainDashboard .dashboardRow .adminSections .tabs .link{
    display: flex;
}
.mainDashboard .dashboardRow .adminSections .tabs h2{
    font-size: 18px;
    margin: 0px;
    padding: 0px;
    margin-left: 13px;
}
.mainDashboard .dashboardRow .adminNotification{
    margin-right: 25px;
    font-size: 30px;
    color: #111D5E;
}
.mainDashboard .dashboardRow .containerOne hr{
    width: 280px;
}

.mainDashboard .dashboardRow .logout{
    display: flex;
    margin-top: 220px;
    color: #FF0000;
    margin-left: 30px;
    align-items: center;
}
.mainDashboard .dashboardRow .logout h2{
    margin: 0px;
    margin-left: 16px;
    font-size: 18px;
    padding: 0px;
}


/* Dashboard */
.dashboard{
    margin: 0px;
    padding: 0px;
    height: 100%;
    height: 100vh;
}
.dashboard .activityRow{
    margin: 0px;
    padding: 0px 10px;
}
.dashboard .activityRow .activityCol{
    margin: 0px;
    padding: 0px;
    align-items: center;
}
.dashboard .activityRow .activityCol .activityInfo{
    margin: 20px 10px;
    margin-bottom: 0px;
    text-align: center;
    background: #fff;
    height: 267px;
    align-items: center;
}
.dashboard .activityRow .activityCol .activityInfo h2{
    font-size: 18px;
    color: #111D5E;
    margin: 0px;
    padding-top: 31px;
    font-weight: bold;
}
.dashboard .activityRow .activityCol .activityInfo h1{
    font-size: 80px;
    color: #A6B1C2;
    margin-top: 22px;
    margin-bottom: 22px;
}
.dashboard .activityRow .activityCol .activityInfo button{
    height: 50px;
    width: 200px;
    color: #fff;
    background: #F37121;
    border-width: 0px;
    border-radius: 3px;
    font-size: 15px;
    outline: none;
}

/* post history */
.allPostHistory{
    margin: 20px;
    background: #fff;
    padding: 20px;
    height: 100%;
    height: 94vh;
    overflow-y: scroll;
}
.allPostHistory .title{
    font-size: 15px;
    font-weight: bold;
    color: #111D5E80;
    margin-bottom: 15px;
}
.allPostHistory .historyTitle .history{
    color: #a8b5c7;
    font-size: 14px;
}
.allPostHistory .historyTitle .history h4{
    color: #a8b5c7;
    font-weight: 400;
    font-size: 14px;
}
.allPostHistory .historyTitle .history h6{
    color: #a8b5c7;
    cursor: text;
}
.history{
    display: flex;
    margin: 10px;
    align-items: center;
    justify-content: space-between;
}
.history h5, .history h6, .history p{
    font-size: 14px;
    margin: 0px;
}
.history h5, .history p{
    width: 120px;
    font-weight: 450;
}
.history h4{
    font-size: 15px;
    width: 350px;
    height: 15px;
    overflow-y: hidden;
    overflow-x: hidden;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    color: #111D5E;
}
.history h6{
    color: #004CFF;
    font-weight: 400;
    cursor: pointer;
}


/* Create Post Section */
.addPost{
    margin: 0px;
    padding: 20px;
    height: 100vh;
}
.addPost .postForm{
    width: 100%;
    background: #fff;
    padding: 20px;
    height: 100%;
}

.addPost .postForm input, .addPost .postForm textarea{
    width: 100%;
    height: 50px;
    margin-bottom: 20px;
    padding: 12px;
    outline: none;
    background: #ABA7D510;
    border-width: 0px;
    border: 1px solid #111D5E20;
}
.addPost .postForm input::-webkit-input-placeholder, .addPost .postForm textarea::-webkit-input-placeholder{
    color: #0d13366c;
    font-size: 14px;
}
.addPost .postForm input:-ms-input-placeholder, .addPost .postForm textarea:-ms-input-placeholder{
    color: #0d13366c;
    font-size: 14px;
}
.addPost .postForm input::placeholder, .addPost .postForm textarea::placeholder{
    color: #0d13366c;
    font-size: 14px;
}
.addPost .postForm textarea{
    height: 375px;
    margin-bottom: 0px;
}
.addPost .postForm .formRow .button{
    margin: auto;
    height: 48px;
    width: 200px;
    background: #F37121;
    border-width: 0px;
    color: #fff;
    font-weight: 600;
    font-size: 18px;
    margin-top: 20px;
    outline: none;
}
.postReaction{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.mainPostRow form{
    display: flex;
    flex-wrap: wrap;
}
.mainPostRow input{
    width: 100%;
    width: 100%;
    height: 50px;
    margin-bottom: 20px;
    padding: 12px;
    outline: none;
    background: #ABA7D510;
    border-width: 0px;
    border: 1px solid #111D5E20;
}
.mainPostRow textarea{
    width: 100%;
    width: 100%;
    height: 200px;
    margin-bottom: 20px;
    padding: 12px;
    outline: none;
    background: #ABA7D510;
    border-width: 0px;
    border: 1px solid #111D5E20;
}
.mainPostRow button{
    margin: auto;
    margin-top: 5px;
    height: 48px;
    width: 200px;
    background: #F37121;
    border-width: 0px;
    color: #fff;
    font-weight: 600;
    font-size: 16px;
    outline: none;
}
p{
    cursor: pointer;
}
.mainPostRow p:hover{
    color: #002642;
}
